import React from "react";

const Program = ({ currentLanguage }) => {
  return (
    <section className="event" id="program">
      {currentLanguage === "hu" ? (
        <div className="container">
          <div className="program">
            <h1>PROGRAMOK</h1>
            <table>
              <tbody>
                <tr>
                  <th className="tableWidth"></th>
                  <th></th>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>6:30-8:30</h4>
                  </td>
                  <td>
                    <strong>Autbuszok rkezse, belltsa</strong>
                  </td>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>9:00</h4>
                  </td>
                  <td>
                    <strong>Megnyit</strong>
                  </td>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>9:30-14:00</h4>
                  </td>
                  <td>
                    <p>
                      Vrosi nosztalgia jratok kzlekednek, melyekre a
                      Kztrsasg tri kijellt autbusz megllhelyen lehet
                      felszllni elre megvltott menetjeggyel. A jratoknak
                      tkzben nincs megllhelye, le-, illetve felszlls csak
                      a vgllomson lehetsges. Az autbuszok frhelye
                      korltozott, krjk ezt vegyk figyelembe felszllskor s
                      a problmamentes utazsrt a kalauzok krseit tartsk be.
                    </p>
                    <p>A jratok az albbi tvonalon kzlekednek:</p>
                    <p>Trkpen:</p>
                    <p>Vrosi nosztalgia autbusz krjratok tvonala</p>
                  </td>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>14:30</h4>
                  </td>
                  <td>
                    <strong>Felvonuls</strong>
                    <p>
                      Felvonuls az sszes jrm rszvtelvel a a festi
                      szpsg Kli-Medencben
                      Tapolca-Gyulakeszi-Kkkt-Kvgrs-Kveskl-Gyulakeszi-Tapolca
                      tvonalon:
                    </p>
                    <p>Trkpen:</p>
                    <p>Felvonuls </p>
                  </td>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>16:20</h4>
                  </td>
                  <td>
                    <strong>Tombola sorsols, zens est</strong>
                  </td>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>23:00</h4>
                  </td>
                  <td>
                    <strong>A rendezvny zrsa, jrmvek killsa</strong>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      ) : (
        <div className="container">
          <div className="program">
            <h1>PROGRAMMES</h1>
            <table>
              <tbody>
                <tr>
                  <th className="tableWidth"></th>
                  <th></th>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>6:30-8:30</h4>
                  </td>
                  <td>
                    <strong>Arrival and parking of buses</strong>
                  </td>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>9:00</h4>
                  </td>
                  <td>
                    <strong>Opening ceremony</strong>
                  </td>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>9:30-14:00</h4>
                  </td>
                  <td>
                    <p>
                      Nostalgic city-bus services operate in town. This is a non
                      stopping service so boarding and disembarking is only
                      allowed at the Kztrsasg square bus stop (event site)
                      with a redeemed ticket. Keep in mind that the capacity of
                      the old timer vehicles is limited so to avoid any
                      incidents please cooperate with the ticket controllers
                      when boarding a bus! The tickets are valid for one round
                      only!
                    </p>
                    <p>Route of the nostalgic city-bus services:</p>
                    <p>On the map:</p>
                    <p>Route of the nostalgic city-bus services</p>
                  </td>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>14:30</h4>
                  </td>
                  <td>
                    <strong>Old timer bus parade</strong>
                    <p>
                      Old timer bus parade featuring all attending vehicles in
                      the picturesque Kli-basin on the
                      Tapolca-Gyulakeszi-Kkkt-Kvgrs-Kveskl-Gyulakeszi-Tapolca
                      route:
                    </p>
                    <p>On the map:</p>
                    <p>Route of the parade</p>
                  </td>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>16:20</h4>
                  </td>
                  <td>
                    <strong>Tombola draw, musical evening</strong>
                  </td>
                </tr>
                <tr>
                  <td valign="top">
                    <h4>23:00</h4>
                  </td>
                  <td>
                    <strong>Event closing, vehicles leaving</strong>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      )}
    </section>
  );
};

export default Program;
