import React from "react";

const Header = ({ currentLanguage }) => {
  return (
    <header className="header">
      {currentLanguage === "hu" ? (
        <div className="container">
          <div>
            <h1>Ikarus Tallkoz Tapolca</h1>
            <h3>2022. mjus 7.</h3>
          </div>
          <small>Fot: Jzsa Lszl</small>
        </div>
      ) : (
        <div className="container">
          <div>
            <h1>Ikarus Meeting Tapolca</h1>
            <h3>7. May 2022</h3>
          </div>
          <small>Photo: Lszl Jzsa</small>
        </div>
      )}
    </header>
  );
};

export default Header;
